<template>
  <div class="list-item">
    <div class="comment-content">
      <div class="order-detail">
        <order-detail-content
          :detail="orderDetail(item.content.order_id)"
          :detailShow="orderDetailShow"
        ></order-detail-content>
      </div>
      <div class="comment-detail">{{ item }}</div>
      <div class="comment-detail">
        <div>{{ item.content.text }}</div>
        <div class="view-detail" @click="viewDetailsClick" title="点击查看详情">
          {{ operationText }}
        </div>
      </div>
      <div class="audit-operation" v-show="item.status === 3200">
        <div title="填写通过原因" @click="passClick">通过</div>
        <div title="填写拒绝理由" @click="rejectClick">拒绝</div>
      </div>
      <div class="audit-operation" v-show="item.status === 3300">
        <div style="cursor: default">已通过</div>
      </div>
      <div class="audit-operation" v-show="item.status === 3400">
        <div style="cursor: default">已拒绝</div>
      </div>
    </div>
  </div>
</template>

<script>
import request from "../../../../network/request";
import OrderDetailContent from "./OrderDetailContent.vue";
export default {
  name: "e-CommentListItem",
  props: {
    item: {
      type: Object,
      default() {
        return {};
      },
    },
    index: {
      type: Number,
    },
    operationText: {
      type: String,
      default: "查看",
    },
  },
  data() {
    return {
      orderDetailShow: [],
      user_id: -1,
    };
  },
  methods: {
    passClick() {
      this.$bus.$emit("passClick", this.index);
    },
    rejectClick() {
      this.$bus.$emit("rejectClick", this.index);
    },
    async orderDetail(orderID) {
      // console.log(orderID);
      const response_1 = await request({
        method: "post",
        url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/order/get",
        data: { id: orderID },
        auth: {
          username:
            "NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4",
          password:
            "NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366",
        },
        headers: {
          "Content-Type": "application/json",
        },
      });
      const response_2 = await request({
        method: "post",
        url: "/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/product/get",
        data: { id: response_1.details.product },
        auth: {
          username:
            "NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4",
          password:
            "NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366",
        },
        headers: {
          "Content-Type": "application/json",
        },
      });
      let res = Object.assign(response_2.details, response_1.details);
      console.log("📕", res);
      console.log(response_1.details, "order");
      console.log(response_2.details, "product");
      this.user_id = res.user;
      // this.orderDetailShow.push({ orderid: response_1.order_id });
      return res;
    },
    viewDetailsClick(e) {
      const param = {
        x: e.clientX,
        y: e.clientY,
        index: this.index,
        user_id: this.user_id,
      };
      this.$bus.$emit("viewDetailsClick", param);
    },
  },
  components: {
    OrderDetailContent,
  },
};
</script>

