<template>
  <div>
    {{ show(detail) }}
    <div class="top">
      <div class="top-left">订单号:{{ orderDetail.order_id }}</div>
      <div class="top-right">
        下单时间:{{ timeProcess(orderDetail.date_created) }}
      </div>
    </div>
    <div class="middle">
      <div class="img">
        <img
          v-lazy="orderDetail.horizontal_shows[0].url"
          alt=""
          style="width: 150px; height: 100px; border-radius: 10px"
        />
      </div>
      <div class="middle-right">
        <div class="title">{{ orderDetail.topic }}}</div>
        <div class="activityDays">
          {{
            activityDays(
              orderDetail.activity_start_time,
              orderDetail.activity_end_time
            )
          }}
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="paid">实付款:</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-OrderDetailContent",
  props: {
    detail: {
      type: Promise,
    },
    detailShow: {
      type: Array,
    },
  },
  data() {
    return {
      orderDetail: {},
    };
  },
  activated() {
    // console.log();
  },
  methods: {
    show(res) {
      res.then((result) => {
        undefined;
        // console.log("result", result);
        this.orderDetail = result;
      });
      //   console.log(this.orderDetail);
    },
    timeProcess(time) {
      //   time = String(time);
      //   console.log(typeof time);
      if (time === undefined) return "";
      time = String(
        time.slice(0, 4) +
          "." +
          time.slice(5, 7) +
          "." +
          time.slice(8, 10) +
          " " +
          time.slice(11, 19)
      );
      return time;
    },
    activityDays(startTime, endTime) {
      startTime = String(startTime.slice(5, 7) + "/" + startTime.slice(8, 10));
      endTime = String(endTime.slice(5, 7) + "/" + endTime.slice(8, 10));
      let days = startTime + "-" + endTime;
      return days;
    },
  },
};
</script>

